/* spin-loader */
.sk-chase {
    position: relative;
    animation: sk-chase 2.5s infinite linear both;
    width: 40px;
    height: 40px;

    &-form {
        margin-left: auto;
        margin-right: auto;

        @include md {
            width: 50px;
            height: 50px;
        }
    }

    &-btn {
        width: 15px;
        height: 15px;
        margin-right: 10px;
        margin-left: auto;

        @include md {
            width: 30px;
            height: 30px;
            margin-right: 15px;
        }
    }

    &-dot {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        animation: sk-chase-dot 2s infinite ease-in-out both;
        &::before {
            content: '';
            display: block;
            width: 25%;
            height: 25%;
            background-color: black;
            border-radius: 100%;
            animation: sk-chase-dot-before 2s infinite ease-in-out both;
        }
        &:nth-child(1) {
            animation-delay: -1.1s;
        }
        &:nth-child(2) {
            animation-delay: -1s;
        }
        &:nth-child(3) {
            animation-delay: -0.9s;
        }
        &:nth-child(4) {
            animation-delay: -0.8s;
        }
        &:nth-child(5) {
            animation-delay: -0.7s;
        }
        &:nth-child(6) {
            animation-delay: -0.6s;
        }
        &:nth-child(1):before {
            animation-delay: -1.1s;
        }
        &:nth-child(2):before {
            animation-delay: -1s;
        }
        &:nth-child(3):before {
            animation-delay: -0.9s;
        }
        &:nth-child(4):before {
            animation-delay: -0.8s;
        }
        &:nth-child(5):before {
            animation-delay: -0.7s;
        }
        &:nth-child(6):before {
            animation-delay: -0.6s;
        }
    }
}

.absolute-loader .sk-chase-dot::before {
    background-color: white;
}

@keyframes sk-chase {
    100% {
        transform: rotate(360deg);
    }
}
@keyframes sk-chase-dot {
    80%,
    100% {
        transform: rotate(360deg);
    }
}
@keyframes sk-chase-dot-before {
    50% {
        transform: scale(0.4);
    }
    100%,
    0% {
        transform: scale(1);
    }
}

/* cat-loader */
.lds-spinner {
    color: official;
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    & div {
        transform-origin: 35px 35px;
        animation: lds-spinner 1.2s linear infinite;
    }
    & div:after {
        content: ' ';
        display: block;
        position: absolute;
        top: 3px;
        left: 30px;
        width: 5px;
        height: 16px;
        border-radius: 20%;
        background: #fff;
    }
    & div:nth-child(1) {
        transform: rotate(0deg);
        animation-delay: -1.1s;
    }
    & div:nth-child(2) {
        transform: rotate(30deg);
        animation-delay: -1s;
    }
    & div:nth-child(3) {
        transform: rotate(60deg);
        animation-delay: -0.9s;
    }
    & div:nth-child(4) {
        transform: rotate(90deg);
        animation-delay: -0.8s;
    }
    & div:nth-child(5) {
        transform: rotate(120deg);
        animation-delay: -0.7s;
    }
    & div:nth-child(6) {
        transform: rotate(150deg);
        animation-delay: -0.6s;
    }
    & div:nth-child(7) {
        transform: rotate(180deg);
        animation-delay: -0.5s;
    }
    & div:nth-child(8) {
        transform: rotate(210deg);
        animation-delay: -0.4s;
    }
    & div:nth-child(9) {
        transform: rotate(240deg);
        animation-delay: -0.3s;
    }
    & div:nth-child(10) {
        transform: rotate(270deg);
        animation-delay: -0.2s;
    }
    & div:nth-child(11) {
        transform: rotate(300deg);
        animation-delay: -0.1s;
    }
    & div:nth-child(12) {
        transform: rotate(330deg);
        animation-delay: 0s;
    }
}

@keyframes lds-spinner {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
